@use 'base/variables' as v;
@use 'base/paleta' as color;

.hero {
    width: 100%;
    height: 100vh;
    background-color: color.$color-primary-4 ;

    background: linear-gradient(0deg, rgba(55,14,77,0.8603816526610644) 0%, rgba(9,9,121,0.5858718487394958) 48%, rgba(0,212,255,0.45702030812324934) 100%), url('/build/img/globos.jpg');
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
    transition: all;
    transition-duration: 5s;
    .hero__titulo{
        margin: 10rem auto 0 auto;
        display: flex;
        flex-direction: column;
        color:color.$color-blanco;
        justify-content: center;
            align-items: center;
            text-align: center;
        h1{

            
        }
    }
    

}
.hero:hover{
    //background-position: right bottom;
}

.hero__boton{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    border: 1px solid color.$color-gris-3;
    border-radius: 50%;
    text-align: center;
    margin: 0 auto;
    background-color: transparent;
    transition: all;
    transition-duration: .6s;
    font-size: 3rem;
    a{
        color:color.$color-gris-1;
    }
}
.hero__boton:hover{
    border-color: color.$color-gris-0;
    background-color:  color.$color-gris-2;

}
.navegacion{
    width: 100%;
    display: flex;
    justify-content:flex-end;
    margin: 0 auto;
    padding: 2rem;
    border-radius: 10px;
    
    //background-color: rgba($color: color.$color-primary-0, $alpha: .7);
   //box-shadow: 0 10px 40px color.$color-primary-0;
    text-align: center;
    .enlaces{
        font-size: 2.2rem;
        font-weight: 300;
        a{
            padding: 0 1rem;
            color: color.$color-blanco;
            transition: all;
            transition-duration: 1s;
            &.actual{
                color: color.$color-blanco;
                //background-color: color.$color-secondary-1-0;
            }
        }
        a:hover{
            background-color: color.$color-secondary-1-0;
        }
    }
    
}
